.messengers {
    margin-top: -4px;

    &__list {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    &__link {
        margin-left: 3px;
        line-height: 0;

        &:first-child {
            margin-left: 0;
        }
    }

    &__description {
        display: block;
        margin-top: 6px;
        font-size: 12px;
    }
}