.awards {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    margin-top: 9px;
    padding-right: 23px;

    &__logo, &__heading, &__link, &__list {
        flex-shrink: 0;
    }

    &__logo {
        margin-right: 9px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    &__heading {
        margin-top: -3px;
        margin-bottom: 6px;
        line-height: 17px;
    }

    &__link {
        margin-bottom: 5px;
        color: #515151;
        font-size: 12px;
    }
}