.header {
    padding-top: 54px;

    &__top {
        display: flex;
        justify-content: center;
        width: 100%;
        background-color: rgba(243,243,243, 0.8);
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
    }

    &__middle {
        @include container;
        @include row;

        margin-bottom: 10px;
        align-items: center;
    }
}