.telegram {
    display: block;
    width: 26px;
    height: 26px;  
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url('~@/assets/images/icons/telegram.png');

    &:hover {
        background-image: url('~@/assets/images/icons/telegram-active.png');
    }
}